import React, { Component, Fragment } from 'react';

import { Card, Row, Table, Popconfirm, Col, message } from 'antd';
import { getListStudyField, deleteStudyField } from 'services/StudyField';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';

import { specializedTrainings } from 'dump/data';

class SpecializedTraining extends Component {
  constructor(props) {
    super(props);
    this.state = {
      specialized: [],
      loading: false,
      pagination: {},
    };

    this.columns = [
      {
        title: 'Ngành đào tạo',
        dataIndex: 'parentLabel',
        key: 'parentLabel',
      },
      {
        title: 'Mã chuyên ngành',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên chuyên ngành',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          specializedTrainings.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#/specializeTraining/form/edit/${record.code}`} />
              <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
  }
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListStudyField(page, true, true)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          specialized: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = (pagination, filters, sorter) => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteStudyField(e)
      .then(() => {
        message.success('Xóa ngành đào tạo thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.message);
      });
  };

  render() {
    const { pagination, specialized, loading } = this.state;
    return (
      <Card title="Danh sách chuyên ngành">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/specializeTraining/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              dataSource={specialized}
              columns={this.columns}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SpecializedTraining />
  </Page>
);



